export const translateMonth = name => {
  switch (name) {
    case '':
      return 'January';
    case '':
      return 'February';
    case '':
      return 'March';
    case '':
      return 'April';
    case '':
      return 'May';
    case '':
      return 'June';
    case '':
      return 'July';
    case '':
      return 'August';
    case '':
      return 'September';
    case '':
      return 'October';
    case '':
      return 'November';
    case '':
      return 'December';
    default:
      break;
  }
};
